@font-face {
  font-family: "Raleway";
  src: url("./fonts/Raleway-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Raleway";
  src: url("./fonts/Raleway-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
*, *:before, *:after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}

body {
  position: relative;
  margin: 0;
  color: #212121;
  font-family: "Raleway", Helvetica, Arial, sans-serif;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-focus-ring-color: rgba(255, 255, 255, 0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: none;
}

ul, ol {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

address {
  margin: 0;
  font-style: normal;
}

img,
video {
  max-width: 100%;
}

img {
  border: 0;
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}
p + p {
  margin-top: 30px;
}

a {
  color: #212121;
  text-decoration: none;
  transition: color 0.25s ease;
}
a:hover {
  color: #E00C0C;
  text-decoration: none;
}

input,
textarea {
  width: 100%;
  padding: 0;
  border: none;
  font-family: "Raleway", Helvetica, Arial, sans-serif;
  background-color: transparent;
}

textarea {
  resize: none;
}

button {
  font-family: "Raleway", Helvetica, Arial, sans-serif;
  text-align: center;
  border: none;
  background: none;
  cursor: pointer;
}

label {
  margin-bottom: 0;
}

.container {
  max-width: 1920px;
}

@media (max-width: 1023px) {
  [class*=col] {
    padding: 0 7.5px;
  }
}

.title {
  font-size: 50px;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: -0.01em;
}
@media (max-width: 1439px) {
  .title {
    font-size: 40px;
  }
}
@media (max-width: 1023px) {
  .title {
    font-size: 34px;
  }
}
.title--block {
  font-size: 25px;
  line-height: 1.4;
}
@media (max-width: 1439px) {
  .title--block {
    font-size: 22px;
  }
}
@media (max-width: 1023px) {
  .title--block {
    font-size: 20px;
  }
}
.title + * {
  margin-top: 15px;
}

.text {
  font-size: 16px;
  line-height: 1.625;
}
@media (max-width: 1439px) {
  .text {
    font-size: 15px;
  }
}
@media (max-width: 1023px) {
  .text {
